  font-family: "PTMono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
  /* iOS Safari */
  -webkit-touch-callout: none;
  /* Safari */
  -webkit-user-select: none;
  /* Konqueror HTML */
  -khtml-user-select: none;
  /* Old versions of Firefox */
  -moz-user-select: none;
  /* Internet Explorer/Edge */
  -ms-user-select: none;
  /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
  user-select: none;
}
.contacts-list__link:focus, .contacts-list__link:focus-visible {
  padding-top: 10px;
  box-shadow: none;
  outline: 0;
}
.contacts-list__link:hover, .contacts-list__link:focus {
  color: var(--color-pacific-blue);
  text-shadow: 0 0 8px var(--color-pacific-blue);
  background-color: transparent;
}
.contacts-list__link:active {
  color: var(--color-cerulean);
}
.contacts-list__link:disabled {
  color: var(--color-black-pearl);
  background-color: var(--color-alice-blue2);
}
.contacts-list__link:focus:not(:focus-visible) {
  box-shadow: none;
  outline: 0;
}
.contacts-list__link:focus-visible {
  box-shadow: 0 0 1px #2c39f2, 0 0 2px #2c39f2, 0 0 6px #2c39f2, 0 0 6px #2c39f2, inset 0 0 1px #2c39f2, inset 0 0 2px #2c39f2, inset 0 0 6px #2c39f2, inset 0 0 6px #2c39f2;
  outline-offset: -2px;
  z-index: 50;
}

.intro {
  min-width: 320px;
  padding-bottom: 280px;
  margin-bottom: 100px;
  background-color: var(--color-black-pearl);
}
@media (max-width: 1023px) {
  .intro {
    padding-bottom: 0;
    margin-bottom: 300px;
  }
}

.intro__bg-image {
  width: 1920px;
  height: 880px;
  min-width: 320px;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 50%;
  object-fit: cover;
  transform: translate(-50%, 0);
}
@media (max-width: 1023px) {
  .intro__bg-image {
    height: 640px;
    object-position: -125px top;
  }
}
@media (max-width: 374px) {
  .intro__bg-image {
    height: 560px;
    object-position: -230px top;
  }
}

.intro h2 {
  margin: 0;
  padding-top: 304px;
  margin-left: 125px;
  margin-bottom: 32px;
  position: relative;
  z-index: 2;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: var(--color-alice-blue);
}
@media (max-width: 1023px) {
  .intro h2 {
    padding-top: 180px;
    margin-left: 0;
    margin-bottom: 0;
    font-size: 64px;
    line-height: 75px;
    letter-spacing: 0.07em;
  }
}
@media (max-width: 374px) {
  .intro h2 {
    padding-top: 150px;
    padding-bottom: 8px;
    font-size: 36px;
    line-height: 42px;
  }
}

.intro p {
  margin: 0;
  margin-bottom: 46px;
  margin-left: 125px;
  width: 685px;
  position: relative;
  z-index: 2;
  font-family: "PTMono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 155%;
  color: var(--color-alice-blue);
}
@media (max-width: 1023px) {
  .intro p {
    margin-left: 0;
    margin-bottom: 56px;
    width: auto;
  }
}
@media (max-width: 374px) {
  .intro p {
    margin-bottom: 45px;
    font-family: "PTMono", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 155%;
  }
}

.button--intro {
  margin: 0 auto;
  margin-left: 125px;
  position: relative;
  z-index: 2;
}
@media (max-width: 1023px) {
  .button--intro {
    margin-left: 45px;
  }
}
@media (max-width: 600px) {
  .button--intro {
    margin-left: 0;
    width: 270px;
    min-height: 40px;
  }
}
@media (max-width: 374px) {
  .button--intro {
    margin: 0;
    margin-left: 0;
    width: 270px;
    min-height: 40px;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

@media (max-width: 600px) {
  .intro__button-text1 {
    display: none;
  }
}

@media (min-width: 601px) {
  .intro__button-text2 {
    display: none;
  }
}

.features {
  margin: 0;
  margin-top: 120px;
  background-color: #ffffff;
}
@media (max-width: 1023px) {
  .features {
    margin-top: 80px;
  }
}
@media (max-width: 767px) {
  .features {
    margin-top: 50px;
  }
}

.features__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, minmax(595px, 1fr));
  gap: 30px;
  list-style: none;
}
@media (max-width: 1439px) {
  .features__list {
    grid-template-columns: repeat(2, minmax(320px, 1fr));
  }
}
@media (max-width: 767px) {
  .features__list {
    grid-template-columns: minmax(270px, 1fr);
  }
}

.features__item {
  margin: 0;
  padding: 40px 19px 20px 43px;
  min-height: 300px;
  border: 1px solid var(--color-whisper);
  border-left: 5px solid var(--color-deep-sky-blue);
  cursor: default;
  transition: 0.3s;
}
@media (max-width: 1023px) {
  .features__item {
    padding: 40px 36px 28px 40px;
    min-height: 360px;
  }
}
@media (max-width: 767px) {
  .features__item {
    padding: 31px 30px 0 25px;
    min-height: 340px;
  }
}

.features__item:first-child {
  background-color: var(--color-deep-sky-blue);
}

.features__item:first-child svg,
.features__item:first-child h3,
.features__item:first-child p {
  color: var(--color-default-white);
}

.features__item-icon {
  width: 35px;
  height: 35px;
  color: var(--color-deep-sky-blue);
}
@media (max-width: 767px) {
  .features__item-icon {
    width: 30px;
    height: 30px;
  }
}

.features__item h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  margin: 0;
  padding: 14px 0 14px;
  line-height: 34px;
  color: var(--color-deep-sky-blue);
}
@media (max-width: 1023px) {
  .features__item h3 {
    padding: 7px 0 8px;
  }
}
@media (max-width: 767px) {
  .features__item h3 {
    padding: 9px 0 9px;
    font-size: 20px;
    line-height: 28px;
  }
}

.features__item p {
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  margin: 0;
  line-height: 22px;
  color: var(--color-black-pearl);
}
@media (max-width: 767px) {
  .features__item p {
    font-size: 14px;
    line-height: 19px;
  }
}

.features__item:hover {
  background-color: var(--color-deep-sky-blue);
}
.features__item:hover * {
  color: var(--color-default-white);
}
.features__item:hover svg {
  fill: var(--color-default-white);
}

@media (max-width: 767px) {
  .about {
    margin: 48px 0 80px;
  }
}

.about__container {
  margin-top: 123px;
  display: grid;
  grid-template-areas: "img text" "img btn";
  grid-template-columns: 150px 1fr;
  grid-column-gap: 100px;
}
@media (max-width: 1023px) {
  .about__container {
    margin-top: 78px;
    grid-column-gap: 25px;
  }
}
@media (max-width: 767px) {
  .about__container {
    margin-top: 0;
    display: block;
  }
}

.about__img-wrapper {
  grid-area: img;
}
@media (max-width: 1023px) {
  .about__img-wrapper {
    width: 150px;
    height: 320px;
    position: relative;
  }
}
@media (max-width: 767px) {
  .about__img-wrapper {
    display: none;
  }
}

@media (max-width: 1023px) {
  .about__img-wrapper img {
    position: absolute;
    top: 2px;
    right: 4px;
  }
}

.about__text-wrapper {
  max-width: 970px;
  overflow: hidden;
  grid-area: text;
}
@media (max-width: 1023px) {
  .about__text-wrapper {
    padding-right: 0;
  }
}

.about__title {
  margin: 0;
  padding-bottom: 12px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: var(--color-black-pearl);
}
@media (max-width: 767px) {
  .about__title {
    font-size: 28px;
    line-height: 40px;
    padding-bottom: 10px;
  }
}

.about p {
  margin: 0;
  margin-bottom: 22px;
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0;
  color: var(--color-black-pearl);
}
@media (max-width: 767px) {
  .about p {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 16px;
  }
}

@media (max-width: 1023px) {
  .about__text--tablet {
    display: block;
  }
}
@media (max-width: 767px) {
  .about__text--tablet {
    display: none;
  }
}

.about__button {
  width: 180px;
  height: 45px;
  grid-area: btn;
}
.about__button--nojs {
  display: none;
}
@media (max-width: 767px) {
  .about__button {
    width: 160px;
    height: 35px;
  }
}

.products {
  margin-top: 97px;
  padding-bottom: 140px;
  background-color: var(--color-black-pearl);
}

.products__title {
  margin: 0;
  padding-top: 68px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: var(--color-default-white);
}
.products__title--mobile {
  display: none;
}
@media (max-width: 320px) {
  .products__title--mobile {
    display: block;
  }
}
.products__title--desktop {
  display: block;
}
@media (max-width: 320px) {
  .products__title--desktop {
    display: none;
  }
}

.products__swiper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 1439px) {
  .products__swiper {
    flex-direction: column;
  }
}

.swiper {
  width: 350px;
  height: 100%;
}
@media (max-width: 1439px) {
  .swiper {
    width: 670px;
    height: 250px;
    object-fit: cover;
  }
}
@media (max-width: 770px) {
  .swiper {
    width: 270px;
    object-fit: cover;
  }
}

.swiper-img-wrapper img {
  width: 350px;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 1439px) {
  .swiper-img-wrapper img {
    width: 670px;
    height: 250px;
    object-fit: cover;
  }
}
@media (max-width: 770px) {
  .swiper-img-wrapper img {
    width: 270px;
    object-fit: cover;
  }
}

.swiper-img-wrapper p {
  margin: 0;
  position: absolute;
  left: 40px;
  top: 50px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: var(--color-default-white);
}

.swiper-img-wrapper .swiper-button-prev {
  display: none;
}

.swiper-button-next {
  position: absolute;
  top: 620px;
  left: 306px;
}
@media (max-width: 1439px) {
  .swiper-button-next {
    top: 220px;
    left: 626px;
  }
}
@media (max-width: 770px) {
  .swiper-button-next {
    top: 220px;
    left: 226px;
  }
}

.swiper-button-next:after {
  width: 60px;
  height: 60px;
  display: block;
  box-sizing: border-box;
  position: absolute;
  background-color: var(--color-deep-sky-blue);
  z-index: 2;
  --swiper-navigation-size: 0;
}

.swiper-button-next svg {
  z-index: 4;
}

